			obj.hint = () => this.$.$mol_locale.text( '$hyoo_search_app_Result_ban_hint' )
			obj.Filter = () => null as any
			obj.align_hor = () => "left"
			obj.options = () => this.result_ban_options(id)
			obj.value = (val?: any) => this.result_ban(id, val)
			
